<template>
  <div class="h-full w-full flex justify-center">
    <div class="w-80 mx-auto absolute z-10 my-12 flex-col">
      <gmap-autocomplete
        class="w-full p-2 shadow rounded focus:outline-none focus:ring-2 focus:ring-medehanite-primary focus:border-transparent"
        :options="{
          fields: ['geometry', 'formatted_address', 'address_components'],
        }"
        @place_changed="setPlace"
        required
      >
      </gmap-autocomplete>
      <div v-for="(m, index) in markers" :key="index">
        {{ m.icon }}
      </div>
    </div>

    <gmap-map
      :center="center"
      :zoom="16"
      class="h-full w-full"
      @dragend="dragend"
      @center_changed="updateCenter"
    >
      <gmap-info-window
        :options="infoOptions"
        :position="infoWindowPos"
        :opened="infoWinOpen"
        @closeclick="infoWinOpen = false"
      >
        <div v-html="infoContent"></div>
      </gmap-info-window>
      <gmap-marker
        :key="index + m.position"
        v-for="(m, index) in markers"
        :position="m.position"
        @click="(center = m.position), toggleInfoWindow(m, index)"
        :icon="{
          url: require(m.type == 'Clinic'
            ? '../../assets/markers/clinic.svg'
            : m.type == 'Hospital'
            ? '../../assets/markers/hospital.svg'
            : '../../assets/markers/pharmacy.svg'),
        }"
      ></gmap-marker>
    </gmap-map>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { getDistance } from "geolib";

export default {
  name: "GoogleMap",
  data() {
    return {
      center: { lng: 9.003709899999999, lat: 38.789150299999996 },
      markers: [],
      places: [],
      mapCenter: {},
      currentPlace: null,
      last_location: { lng: 9.003709899999999, lat: 38.789150299999996 },

      //********************************* */
      infoContent: "",
      infoWindowPos: {
        lat: 0,
        lng: 0,
      },
      infoWinOpen: false,
      currentMidx: null,
      //optional: offset infowindow so it visually sits nicely on top of our marker
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35,
        },
      },
    };
  },
  watch: {
    pharmacies() {
      this.markers = [];
      this.addMarker();
    },
  },
  computed: {
    ...mapGetters("nearByMod", {
      pharmacies: "pharmacies",
    }),
    // pharmacies(){

    // }
  },
  mounted() {
    window.addEventListener("cat", this.anything);
    this.geolocate();
    this.addMarker();
    this.$eventbus.$on("change-center", (val) => {
      this.changeCenter(val);
    });
  },
  destroyed() {
    window.removeEventListener('cat',this.anything)
  },

  methods: {
    anything (e) {
      this.$router.push({
        path: "phar-detail",
        query: { id: e.detail.id },
      });
    },
    setPlace(place) {
      if (place.geometry) {
        this.center = place.geometry.location;
        this.$eventbus.$emit("loading-nearBy", true);

        this.$store
          .dispatch("nearByMod/getPharmacyByLocation", {
            longitude: place.geometry.location.lng(),
            latitude: place.geometry.location.lat(),
          })
          .finally(() => {
            this.$eventbus.$emit("loading-nearBy", false);
          });
      }
      this.currentPlace = place;
    },
    addMarker() {
      if (this.currentPlace || this.pharmacies.length) {
        console.log("add marker");
        for (var i = 0; i < this.pharmacies.length; i++) {
          const marker = {
            lat: this.pharmacies[i].latitude,
            lng: this.pharmacies[i].longitude,
          };
          this.markers.push({
            position: marker,
            type: this.pharmacies[i].type,
            name: this.pharmacies[i].name,
            id: this.pharmacies[i].id,
          });
        }
      }
    },
    geolocate: function () {
      navigator.geolocation.getCurrentPosition((position) => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        };
        this.last_location = {
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        };
      });
    },
    populator() {
      console.log(this.pharmacies);
      this.pharmacies.forEach((marker) => {
        // create a HTML element for each feature
        var el = document.createElement("div");
        // el.className = "marker clinc";
        if (marker.type == "Pharmacy") {
          console.log("pharmacy");
          el.className = "marker pharmacy";
        } else if (marker.type == "Hospital") {
          console.log("hospital");
          el.className = "marker hospital";
        } else {
          console.log("Clinic");
          el.className = "marker clinic";
        }

        console.log(el);
        // make a marker for each feature and add to the map
      });
    },
    dragend() {
      var distance = getDistance(
        { longitude: this.last_location.lng, latitude: this.last_location.lat },
        {
          longitude: this.mapCenter.lng,
          latitude: this.mapCenter.lat,
        }
      );
      console.log(distance);
      if (distance > 500) {
        console.log("search from a new center");
        this.last_location = {
          lat: this.mapCenter.lat,
          lng: this.mapCenter.lng,
        };
        this.$eventbus.$emit("loading-nearBy", true);

        this.$store
          .dispatch("nearByMod/getPharmacyByLocation", {
            longitude: this.mapCenter.lng,
            latitude: this.mapCenter.lat,
          })
          .finally(() => {
            this.$eventbus.$emit("loading-nearBy", false);
          });
      } else {
        console.log("no seaarch");
      }
    },

    changeCenter(center) {
      this.center = {
        lat: center.latitude,
        lng: center.longitude,
      };
    },
    updateCenter(center) {
      var mpCenter = {
        lat: center.lat(),
        lng: center.lng(),
      };
      this.mapCenter = mpCenter;
    },

    //info window
    toggleInfoWindow: function (marker, idx) {
      this.infoWindowPos = marker.position;
      this.infoContent = this.getInfoWindowContent(marker);

      //check if its the same marker that was selected if yes toggle
      if (this.currentMidx == idx) {
        this.infoWinOpen = !this.infoWinOpen;
      }
      //if different marker set infowindow to open and reset current marker index
      else {
        this.infoWinOpen = true;
        this.currentMidx = idx;
      }
    },

    getInfoWindowContent: function (marker) {
      return `<div class="card">
  <div class="card-image cursor-pointer">
  </div>
  <div class="card-content">
    <div class="media">
      <div class="media-content cursor-pointer"  onclick="window.dispatchEvent(new CustomEvent('cat' , { 'detail':{'id':
'${marker.id}'
      }}));">
        <p class="title is-4">${marker.name}</p>
      </div>
    </div>
  </div>
</div>`;
    },
  },
};
</script>