<template>
  <div class="flex flex-col h-screen">
    <top-bar />
    <div class="flex h-screen -mt-14 pt-14">
      <div class="relative">
        <div
          style=""
          class="open transition-all duration-300 h-full absolute md:relative z-20"
          :class="{ close: show }"
        >
          <div
            class="relative z-50 -m-px sm:-mr-14"
            :class="{ '-mr-14': show }"
          >
            <button
              class="p-1 mr-4 absolute top-0 right-0 bg-white rounded-md mt-6 shadow-md"
              :class="{ 'margine-overider': show }"
              @click="show = !show"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                class="h-6 w-6"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M4 6h16M4 12h16M4 18h16"
                />
              </svg>
            </button>
          </div>
          <div class="flex flex-col bg-white h-full px-1 shadow-md gap-1">
            <div class="flex mt-3 mx-1">
              <input
                class="w-44 sm:w-full pl-2 h-14 rounded-md border-2 focus:border-medehanite-primary bg-gray-100"
                type="text"
                name="Med center name"
                id=""
                v-model="pharmacyName"
                @keydown.enter="searchByName"
                placeholder="Medical Center Name"
              />
              <drop-down
                class="w-32 ml-1 h-12"
                placeholder="type"
                :items="types"
                @changed="typeSetter"
              ></drop-down>
            </div>
            <!-- <div v-for="(result, index) in phar.data" :key="index"> -->
            <div class="flex flex-col h-full justify-center" v-if="showLoading">
              <loading-animation
                class="bg-white rounded-full p-3 bg-opacity-75 mx-auto"
              ></loading-animation>
            </div>

            <div
              class="flex-grow h-full w-full overflow-y-scroll"
              v-if="!showLoading"
            >
              <div
                class="max-w-lg h-40"
                v-for="(value, index) in pharmacies"
                :key="index"
              >
                <div
                  class="flex rounded-lg gap-1 h-40 my-4 shadow-md justify-between"
                >
                  <div class="flex gap-1" >
                    <div class="w-1/3 h-full float-left" @click="gotoPharmacyDetail(value.id)">
                      <img
                        class="object-cover h-full rounded-l-lg hover:cursor-pointer shadow-md"
                        :src="storageURL + value.logo"
                        alt=""
                      />
                    </div>

                    <div class="flex flex-col gap-2 float-left w-80 truncate">
                      <div class="flex" @click="gotoPharmacyDetail(value.id)"> 
                        <p class="text-lg text-black truncate cursor-pointer">
                          {{ value.name }}
                        </p>
                      </div>
                      <div class="flex flex-col gap-1">
                        <div class="flex gap-2" @click="gotoPharmacyDetail(value.id)">
                          <svg
                            class="inline-block align-middle"
                            xmlns="http://www.w3.org/2000/svg"
                            width="15.389"
                            height="14.729"
                            viewBox="0 0 10.618 15.168"
                          >
                            <path
                              id="Icon_material-location-on"
                              data-name="Icon material-location-on"
                              d="M12.809,3A5.305,5.305,0,0,0,7.5,8.309c0,3.982,5.309,9.86,5.309,9.86s5.309-5.878,5.309-9.86A5.305,5.305,0,0,0,12.809,3Zm0,7.205a1.9,1.9,0,1,1,1.9-1.9A1.9,1.9,0,0,1,12.809,10.205Z"
                              transform="translate(-7.5 -3)"
                              fill="#14aabb"
                            />
                          </svg>
                          <p
                            class="text-sm align-middle truncate hover:cursor-pointer"
                          >
                            {{ value.relativeLocation }}
                          </p>
                        </div>

                        <div class="flex gap-2" @click="gotoPharmacyDetail(value.id)">
                          <svg
                            class="inline-block align-middle"
                            xmlns="http://www.w3.org/2000/svg"
                            width="15.389"
                            height="14.729"
                            viewBox="0 0 15.389 14.729"
                          >
                            <path
                              id="Icon_awesome-star"
                              data-name="Icon awesome-star"
                              d="M8.31.511,6.432,4.32l-4.2.613A.921.921,0,0,0,1.72,6.5l3.04,2.963-.719,4.185a.92.92,0,0,0,1.335.969l3.759-1.976L12.9,14.62a.921.921,0,0,0,1.335-.969l-.719-4.185L16.551,6.5a.921.921,0,0,0-.509-1.571l-4.2-.613L9.961.511A.921.921,0,0,0,8.31.511Z"
                              transform="translate(-1.441)"
                              fill="#14aabb"
                            />
                          </svg>

                          <span class="text-sm align-middle">
                            {{  Math.round((value.rating + Number.EPSILON) * 100) / 100}}
                          </span>
                        </div>
                        <div class="flex justify-end">
                          <button
                            class="bg-medehanite-primary h-10 w-10 rounded-md p-1 hover:bg-medehanite-focused shadow-md my-auto mr-2 float-right"
                            @click="centerMap(value.longitude ,value.latitude)"
                          >
                            <div class="flex justify-center">
                              <div>
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  width="28.33"
                                  height="28.331"
                                  viewBox="0 0 28.33 28.331"
                                >
                                  <path
                                    id="Icon_awesome-directions"
                                    data-name="Icon awesome-directions"
                                    d="M27.811,12.91,15.42.52a1.775,1.775,0,0,0-2.51,0L.52,12.91a1.775,1.775,0,0,0,0,2.51L12.91,27.811a1.774,1.774,0,0,0,2.51,0L27.811,15.42a1.775,1.775,0,0,0,0-2.51Zm-5.588.695-4.66,4.3a.443.443,0,0,1-.743-.325V14.608H11.509v3.541a.443.443,0,0,1-.443.443H9.3a.443.443,0,0,1-.443-.443V13.723a1.771,1.771,0,0,1,1.771-1.771h6.2V8.979a.443.443,0,0,1,.743-.325l4.66,4.3A.443.443,0,0,1,22.223,13.605Z"
                                    fill="#fff"
                                  />
                                </svg>
                              </div>
                            </div>
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <google-map></google-map>
    </div>
  </div>
</template>
<script>
import TopBar from "../components/headers/Header";
import DropDown from "../components/DropDown";
import { mapGetters } from "vuex";
import LoadingAnimation from "../components/LoadingAnimation.vue";
import GoogleMap from "../components/googleMap/GoogleMap";
export default {
  components: {
    DropDown,
    TopBar,
    LoadingAnimation,
    GoogleMap,
  },
  data() {
    return {
      showLoading: true,
      show: true,
      types: ["All", "Clinic", "Hospital", "Pharmacy"],
      currentLocation: {},
      pharmacyType: "",
      pharmacyName: "",
      medicalCenterType: ""
    };
  },
  computed: {
    ...mapGetters("nearByMod", {
      pharmacies: "pharmacies",
    }),
    storageURL() {
      return process.env.VUE_APP_STORAGE_BASE_URL;
    },
   
  },
  watch: {
    pharmacies() {
      console.log(this.pharmacies);
    },
     medicalCenterType(){
      this.searchByName()
    }
  },
  methods: {
    centerMap(lng,lat){
 this.$eventbus.$emit("change-center", {longitude:lng , latitude:lat});
    },
    typeSetter(value) {
      this.medicalCenterType=value;
      this.$store.dispatch('nearByMod/setState' , this.medicalCenterType);
      console.log(value);
    },
        gotoPharmacyDetail(pharId) {
      this.$router.push({
        path: "phar-detail",
        query: { id: pharId},
      });
    },
    searchByName(){
      if(this.pharmacyName && this.pharmacyName.length>0){
        this.showLoading=true;

    this.$store
      .dispatch("nearByMod/getPharmacyByName", {name:this.pharmacyName , type:this.medicalCenterType})
      .finally(() => {
        this.showLoading = false;
      });
    }
    }
  },
  mounted() {
    this.currentLocation = this.$store.getters["location/currentLocation"];
    this.$eventbus.$on("loading-nearBy", (val) => {
      this.showLoading = val;
    });
    this.$store
      .dispatch("nearByMod/getPharmacyByLocation", this.currentLocation)
      .finally(() => {
        this.showLoading = false;
      });
  },
};
</script>
<style scoped></style>
<style scoped>
.open {
  width: 23rem;
}
.close {
  margin-left: -23rem;
}
</style>
