<template>
  <layout class="overflow-x-hidden">
    <div class="flex justify-center">
      <span
        class="text-4xl text-medehanite-primary italic font-serif mt-6 mb-9"
      >
        News
      </span>
    </div>
    <div class="flex justify-center items-center  w-full h-96" v-if="loading">
      <loading-animation></loading-animation>
    </div>
    <div v-if="!loading">
      <div class="flex justify-center lg:hidden">
        <div class="flex flex-col sm:container items-center">
          <div
            class="flex p-1 cursor-pointer"
            @click="gotoNews(newsList[i].id)"
            v-for="i in 2"
            :key="i"
          >
            <div
              class="flex flex-col gap-4"
              v-if="newsList[i] && newsList[i] != null"
            >
              <img
                v-if="newsList[i].image && newsList[i].image != null"
                :src="storageURL + newsList[i].image"
                class="object-cover max-h-72 sm:max-h-96 w-full px-3"
              />
              <!-- <img :src="storageURL + logo" class="object-cover h-full rounded-l-lg" /> -->
              <div class="flex flex-col gap-1 px-2">
                <div class="text-2xl font-serif text-center">
                  <span>{{ newsList[i].title }}</span>
                </div>
                <div
                  class="text-gray-400 text-center font-light text-lg line-clamp-3"
                >
                  {{ newsList[i].text }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="justify-center lg:flex hidden">
        <div class="container flex gap-1 max-w-5xl">
          <div
            class="flex flex-col gap-4 w-4/6cursor-pointer"
            @click="gotoNews(newsList[0].id)"
            v-if="newsList[0] && newsList[0] != null"
          >
            <img
              v-if="newsList[0].image && newsList[0].image != null"
              :src="storageURL + newsList[0].image"
              class="object-cover max-h-96 w-full pl-2"
            />
            <!-- <img :src="storageURL + logo" class="object-cover h-full rounded-l-lg" /> -->
            <div class="flex flex-col gap-1 px-2">
              <div class="text-2xl font-serif text-center">
                <span>{{ newsList[0].title }}</span>
              </div>
              <div
                class="text-gray-400 text-center font-light text-lg line-clamp-3"
              >
                {{ newsList[0].text }}
              </div>
            </div>
          </div>
          <div class="flex flex-col gap-4 w-2/6">
            <div
              class="cursor-pointer"
              @click="gotoNews(newsList[i].id)"
              v-for="i in 2"
              :key="i"
            >
              <div
                class="flex flex-col gap-4 x-3"
                v-if="newsList[i] && newsList[i] != null"
              >
                <img
                  v-if="newsList[i].image && newsList[i].image != null"
                  :src="storageURL + newsList[i].image"
                  class="object-cover max-h-48 px-4"
                />
                <!-- <img :src="storageURL + logo" class="object-cover h-full rounded-l-lg" /> -->
                <div class="flex flex-col gap-1 px-2">
                  <div class="text-xl font-serif text-center">
                    <span>{{ newsList[i].title }}</span>
                  </div>
                  <div
                    class="text-gray-400 text-center font-light text-base line-clamp-2"
                  >
                    {{ newsList[i].text }}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="my-6">
        <hr />
      </div>
      <div class="flex justify-center">
        <div class="flex flex-col">
          <div
            class="flex p-3 max-w-2xl cursor-pointer"
            @click="gotoNews(newsList.id)"
            v-for="news in newsList"
            :key="news.id"
          >
            <div class="flex flex-col gap-1">
              <div class="text-lg font-medium text-start line line-clamp-2">
                <span>{{ news.title }}</span>
              </div>
              <div
                class="text-gray-400 text-start font-light text-lg line-clamp-2"
              >
                {{ news.text }}
              </div>
              <div
                class="text-gray-600 text-start font-light text-lg line-clamp-2"
              >
                {{ news.created_at | toDate }}
              </div>
            </div>
            <div>
              <img
                v-if="news.image && news.image != null"
                :src="storageURL + news.image"
                class="object-cover max-h-36 max-w-md"
              />
            </div>
          </div>

          <div class="w-full flex justify-center">
            <loading-animation v-if="nPageLoading"> </loading-animation>
            <button
              v-if="!nPageLoading && isNextPage"
              class="px-3 py-2 hover:bg-gray-400 bg-gray-300 text-gray-700 rounded-md hover:text-white"
              @click="nextPage"
            >
              More news
            </button>
          </div>
        </div>
      </div>
    </div>
  </layout>
</template>
<script>
import { mapGetters } from "vuex";
import LoadingAnimation from "../components/LoadingAnimation.vue";
import Layout from "../layouts/Main.vue";
export default {
  name: "News",
  components: {
    Layout,
    LoadingAnimation,
  },
  data() {
    return {
      loading: true,
      nPageLoading: false,
    };
  },
  computed: {
    storageURL() {
      return process.env.VUE_APP_STORAGE_BASE_URL;
    },
    ...mapGetters("newsMod", {
      newsList: "newsList",
      isNextPage: "nextPage",
    }),
  },
  methods: {
    nextPage() {
      this.nPageLoading = true;
      this.$store.dispatch("newsMod/getNewsListByPage").finally(() => {
        this.nPageLoading = false;
      });
    },
    gotoNews(val) {
      this.$router.push({
        path: "news-detail",
        query: { id: val },
      });
    },
  },
  mounted() {
    this.loading = true;
    this.$store.dispatch("newsMod/getNewsList").then((val) => {
      console.log('not' , val);
      if (val.status == 200) {
        this.loading = false;
      } else {
        this.empty = true;
        this.loading = false;
      }
    });
  },
  filters: {
    toDate(value) {
      if (!value) return "";
      var date = new Date(value).toDateString();
      return date;
    },
  },
};
</script>
<style  scoped>
</style>