<template>
  <layer class="overflow-x-hidden">
    <div class="w-full h-full py-10">
      <!-- <h1 class="items-center flex h-screen w-screen justify-center text-3xl text-medehanite-primary">FAQ</h1> -->
      <div class="max-w-4xl w-full flex flex-col gap-5 mx-auto">
        <div
          class="flex w-full py-4 text-medehanite-primary font-medium text-4xl justify-center"
        >
          FAQ
        </div>
        <div
          v-if="loading"
          class="w-full flex justify-center items-center"
        >
          <loading-animation></loading-animation>
        </div>
        <div
          v-else
          v-for="(value, index) in faqs"
          :key="index"
          class="rounded-xl py-7 px-3 sm:px-6 ring-1 ring-gray-200 cursor-pointer bg-gray-50 hover:shadow-xl"
          :class="{ 'shadow-xl': faq_selected === index }"
          @click="
            faq_selected !== index
              ? (faq_selected = index)
              : (faq_selected = null)
          "
        >
          <span class="text-gray-600">
            <i>
              {{ value.question }}
            </i>
          </span>

          <div
            class="flex flex-col relative overflow-hidden transition-all max-h-0 duration-700 text-gray-900"
            :style="
              faq_selected === index
                ? `max-height:  ${$el.parentNode.scrollHeight}px`
                : ``
            "
          >
            <div class="mt-7"></div>
            {{ value.answer }}
          </div>
        </div>
        <div class="flex justify-center mb-9">
          <button
            v-if="faqData && faqData.nextPageUrl != null"
            class="px-3 py-2 hover:bg-gray-400 bg-gray-300 text-gray-700 rounded-md hover:text-white"
            @click="nextPage"
          >
            more FAQs
          </button>
        </div>
      </div>
    </div>
  </layer>
</template>

<script>
import { mapGetters } from "vuex";
import LoadingAnimation from "../components/LoadingAnimation";
import layer from "../layouts/Main.vue";
export default {
  name: "FAQ",
  data() {
    return {
      faq_selected: null,
      loading: true,
    };
  },
  components: {
    layer,
    LoadingAnimation,
  },
  mounted() {
    this.$store.dispatch("faqMod/getFAQ").finally((res) => {
      if (res && res.status && res.status == 200) {
        this.loading = false;
      }else{
        this.loading = false;
      }
     
    });
  },
  computed: {
    ...mapGetters("faqMod", {
      faqs: "faqs",
      faqData: "faqData",
    }),
  },
  methods: {
    nextPage() {
      this.$store.dispatch("faqMod/nextFAQ");
    },
  },
};
</script>