<template>
  <div
    class="dropdown inline-block relative text-black"
    v-click-outside="clickaway"
  >
    <button
      class="rounded-md h-14 w-full pl-6 focus:pl-6 focus:outline-none border-medehanite-inputFieldBorder border-2 bg-medehanite-inputFieldBackgound focus:border-medehanite-primary px-4 -2 text-gray-400 flex items-center justify-start"
      :class="{ [`w-${width}`]: true }"
      @click="showSuggestions = !showSuggestions"
    >
      <div v-if="value == undefined" class="mr-1 w-full justify-start flex">
        <span>
          {{ placeholder }}
        </span>
      </div>
      <div v-if="value != undefined" class="mr-1 w-full justify-start flex">
        <span v-if="value.length != 0">
          {{ value }}
        </span>
      </div>
      <div>
        <svg
          class="fill-current h-4 w-4"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 20 20"
        >
          <path
            d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"
          />
        </svg>
      </div>
    </button>

    <div></div>
    <div
      class="absolute border-2 bg-white rounded-lg shadow-2xl p-2 z-40 ring-8 ring-gray-300 ring-opacity-5"
      v-show="showSuggestions"
    >
      <div
        class="relative bg-white border border-blue-200 rounded-xl h-12 mb-3"
        v-if="searchable"
      >
        <input
          type="text"
          class="absolute pl-10 top-0 h-full w-full rounded-xl focus:ring-2 focus:ring-blue-300"
          placeholder="Search"
        />
        <div
          class="absolute top-0 left-0 rounded-full flex items-center px-1 my-0.5 text-gray-400 justify-center w-10 h-10"
        >
          <svg
            fill="none"
            stroke="currentColor"
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            viewBox="0 0 24 24"
            class="absolute left-2 top-2 w-6 h-6"
          >
            <path d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"></path>
          </svg>
        </div>
      </div>

      <ul class="text-black rounded-lg bg-white">
        <li class="" v-for="(label, id) in items" :key="`${label}-${id}`">
          <div
            class="flex rounded-md text-black hover:bg-gray-200 py-2 h-12 items-center px-4 whitespace-no-wrap cursor-pointer"
            @click="value = label"
            href="#"
          >
            {{ label }}
          </div>
        </li>
      </ul>
    </div>

    <!-- <div class="relative inline-flex">
    <svg class="w-2 h-2 absolute top-0 right-0 m-4 pointer-events-none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 412 232"><path d="M206 171.144L42.678 7.822c-9.763-9.763-25.592-9.763-35.355 0-9.763 9.764-9.763 25.592 0 35.355l181 181c4.88 4.882 11.279 7.323 17.677 7.323s12.796-2.441 17.678-7.322l181-181c9.763-9.764 9.763-25.592 0-35.355-9.763-9.763-25.592-9.763-35.355 0L206 171.144z" fill="#648299" fill-rule="nonzero"/></svg>
    
    <select name="" id="" class="border-2 px-4 py-2 h-12 rounded-lg my-2 bg-white" :class="{
      [`w-${width}`] : true
    }">
      <option value="">sdfasdf</option>
      <option value="">sdfasdf</option>
      <option value="">sdfasdf</option>
    </select>
  </div> -->
  </div>
</template>
<script>
export default {
  props: {
    placeholder: {
      type: String,
    },
    width: {
      type: Number,
      default: 96,
    },
    searchable: {
      type: Boolean,
    },
    items: {
      type: Array,
    },
    api: {
      type: String,
    },
  },
  data() {
    return {
      showSuggestions: false,
      value: "",
    };
  },
  watch: {
    value() {
      this.showSuggestions=false;
      this.$emit("changed", this.value);
      
    },
  },
  methods: {
    clickaway() {
      this.showSuggestions = false;
    },
    clickaway2(value) {
      this.showSuggestions = false;
      this.placeholder = value;
      alert(value);
    },
  },
};
</script>
