<template>
  <layout class="overflow-x-hidden">
    <div class="flex justify-center items-center w-full h-full" v-if="loading">
      <loading-animation></loading-animation>
    </div>
    <div v-if="!loading" class="flex justify-center">
      <div class="flex flex-col max-w-2xl px-3 py-12">
        <div class="text-3xl font-serif text-start pb-8">
          <span
            >{{news.title}}</span
          >
        </div>
        <img
        v-if="news.image"
         :src="storageURL + news.image"
          class="object-cover w-full"
        />
        <p class="text-lg text-gray-700 text-start pt-9">
         {{news.text}}
        </p>
        
      </div>
    </div>
  </layout>
</template>
<script>
import { mapGetters } from "vuex";
import Layout from "../layouts/Main.vue";
import LoadingAnimation from "./LoadingAnimation.vue";
export default {
  name: "NewsDetail",
  components: {
    Layout,
    LoadingAnimation,
  },
  data(){
return {
    loading:true
}
  },
  computed: {
    ...mapGetters("newsMod", {
      news: "news",
    }),
     storageURL() {
      return process.env.VUE_APP_STORAGE_BASE_URL;
    },
  },
  mounted() {
    let parameters = this.$route.query;
    var id = parameters.id;

    this.$store.dispatch("newsMod/getNews", id).then((res) => {
      if (res.status == 200) {
        this.loading = false;
      } else if (res.status == 404) {
        console.log("404");
      } else {
        console.log("404");
      }
    });
  },
};
</script>
