<template>
  <div class="conainer">
    <div class="flex justify-center">
      <loading-animation
        class="bg-white rounded-full p-3 bg-opacity-75"
        v-if="showLoading"
      ></loading-animation>
    </div>
    <div class="flex flex-wrap gap-4 justify-center">
      <div class="">
        <search-place-holder
          v-if="showSearchIndicatorphar && phar.length==0"
          placeholder="Search your Pharmacy"
        ></search-place-holder>
      </div>
      <div v-for="(result, index) in phar" :key="index">
        <div class="max-w-lg h-40">
          <medical-center-card :data="result"></medical-center-card>
        </div>
      </div>
      
    </div>
    <div class="flex justify-center mb-9 pt-9">
        <button
          v-if="pharmacyData.next_page_url != null"
          class="px-3 py-2 hover:bg-gray-400 bg-gray-300 text-gray-700 rounded-md hover:text-white"
          @click="nextPage"
        >
          more medicine
        </button>
      </div>
  </div>
</template>
<script>
import MedicalCenterCard from "../../components/MedicalCenter/MedicalCenterCard.vue";
import SearchPlaceHolder from "../../components/SearchPlaceHolder";
import LoadingAnimation from "../../components/LoadingAnimation.vue";
import { mapGetters } from "vuex";
export default {
  components: { MedicalCenterCard, SearchPlaceHolder, LoadingAnimation },
  data() {
    return {
      showLoading: false,
    };
  },
  methods:{
    nextPage(){
      this.$store.dispatch('searchMedicalCenterMod/nextPharmacies')
    }
  },
  computed: {
    ...mapGetters("searchMedicalCenterMod", {
      phar: "getPharmacies",
      pharmacyData: "getpharmacyData"
    }),
  },
  props: {
    showSearchIndicatorphar: {
      type: Boolean,
      default: true,
    },
  },
  mounted() {
    this.$eventbus.$on("loading-phar", (val) => {
      this.showLoading = val;
    });
  },
};
</script>