<template>
  <secondary class="overflow-x-hidden">
    <div class="w-full bg-medehanite-hero">
      <div class="py-4 flex justify-center mx-auto">
        <div
          v-click-outside="clickAway"
          class="relative shadow-lg border-2 border-gray-50 rounded-full w-80 xsm:w-96 mx-auto bg-red inline-flex text-gray-600 focus-within:text-gray-400"
        >
          <span
            class="absolute rounded-full inset-y-0 left-2 flex items-center px-2"
          >
            <svg
              fill="none"
              stroke="currentColor"
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              viewBox="0 0 24 24"
              class="w-6 h-6 top-0"
            >
              <path d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"></path>
            </svg>
          </span>
          <input
            type="search"
            name="q"
            v-model="searchValue"
            style="width: 650px"
            onfocus="this.placeholder = ''"
            onblur="this.placeholder = 'Search here...'"
            placeholder="Search here..."
            class="py-2 rounded-full text-base text-gray-600 bg-white pl-14 h-12 focus:outline-none focus:bg-white focus:text-gray-900"
            autocomplete="off"
            @focus="showSuggestion = true"
            @click="showSuggestion = true"
            @keydown="search"
            @keydown.enter="$event.target.blur()"
          />
          <div
            class="absolute z-50 bg-white shadow-md rounded-lg w-full my-12 flex-col py-2 text-black"
            v-if="showSuggestion && hasValue"
          >
            <div
              class="bg-white hover:bg-gray-200 w-full h-12 flex transition-transform duration-1000 cursor-pointer"
              @click="setSearchInputValue(value)"
              v-for="(value, index) in suggestions"
              :key="index"
            >
              <span class="flex px-5">
                <svg
                  fill="none"
                  stroke="currentColor"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  viewBox="0 0 24 24"
                  class="w-5 h-5 my-auto justify-center"
                >
                  <path d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"></path>
                </svg>
              </span>

              <p class="my-auto">{{ value }}</p>
            </div>
          </div>
        </div>
      </div>

      <div class="py-4 flex justify-center mx-auto">
        <ul class="list-reset flex border-b gap-4 text-gray-500">
          <li
            class="-mb-px mr-1 inline-block"
            v-for="tab in searchTabs"
            :key="`search-tab-${tab.id}`"
          >
            <router-link
              class="inline-block rounded-t py-2 px-4 text-blue-dark font-semibold"
              :to="`/search?${Object.entries(queries)
                .map(([key, val]) =>
                  key === 'show' ? `show=${tab.id}` : `${key}=${val}`
                )
                .join('&')}`"
              >{{ tab.title }}</router-link
            >
            <hr
              class="w-3/4 mx-auto bg-medehanite-primary h-1 mt-2 px-4"
              v-show="currentTab === tab.id"
            />
          </li>

          <!-- <li class="mr-1">
    <a class="inline-block py-2 px-4 text-grey-light font-semibold" href="#"></a>
  </li> -->
        </ul>
      </div>

      <div class="mt-2 py-1 pb-5">
        <div class="mx-auto flex-1 flex justify-center">
          <div class="text-2xl font-bold pt-5 pb-2">
            Search Result
            <hr class="w-3/4 mx-auto bg-medehanite-primary h-1 mt-2" />
          </div>
        </div>
      </div>
      <div
        v-if="currentTab === 'med'"
        class="flex w-full text-center justify-center"
      >
        <h1 v-if="searchValue">
          Search instead with the name
          <span
            class="cursor-pointer text-blue-600 text-xl"
            @click="addToCart"
            >{{ searchValue }}</span
          >
          in pharmacies!!
        </h1>
      </div>
      <div v-if="true">
        <div v-if="currentTab === 'med'" class="flex justify-center">
          <medications-results
            @modalActivate="mActivate($event)"
            @medDetail="medData($event)"
          ></medications-results>
        </div>
        <div class="pb-8 pt-3" v-if="currentTab === 'phar'">
          <pharmacies-results
            :showSearchIndicatorphar="showSearchIndicatorphar"
          ></pharmacies-results>
        </div>
        <div class="pb-8 pt-3" v-if="currentTab === 'centers'">
          <medical-center-results
            :showSearchIndicatorMedCenter="showSearchIndicatorMedCenter"
          ></medical-center-results>
        </div>
        <!-- <div v-if="modalActivate">
          <medication-detail-modal></medication-detail-modal>
        </div> -->
        <medication-detail-modal
          :show="showModal"
          :detail="medInfo"
        ></medication-detail-modal>
      </div>
    </div>
  </secondary>
</template>
//
<script>
import MedicationsResults from "./Search/MedicationsResults";
import PharmaciesResults from "./Search/PharmaciesResults";
import MedicationDetailModal from "../components/modal/MedicationDetail";
import Secondary from "../layouts/Main";
import MedicalCenterResults from "./Search/MedicalCenterResults.vue";

export default {
  name: "Search",
  components: {
    MedicationsResults,
    PharmaciesResults,
    MedicationDetailModal,
    Secondary,
    MedicalCenterResults,
  },
  data: () => ({
    showModal: false,
    currentTab: null,
    queries: {},
    searchValue: "",
    hasValue: false,
    showLoading: false,
    showSearchIndicatorMedCenter: true,
    showSearchIndicatorphar: true,
    medInfo: {},
    searchTabs: [
      {
        title: "Medications",
        id: "med",
      },
      {
        title: "Pharmacies",
        id: "phar",
      },
      {
        title: "Medical Centers",
        id: "centers",
      },
      // {
      //   title: "Doctors",
      //   id: "doc",
      // },
    ],
    showSuggestion: false,
  }),
  beforeMount() {
    console.log("##############");
    console.log("this.queries.show", this.queries.show);
    console.log("##############");
      this.queries = this.$route.query;
    if (!this.queries.show) {
      this.queries.show
       = "med";
    }
    this.searchByCategory();
  },
  created() {},
  computed: {
    suggestions() {
      let value = this.$store.getters["suggestion/suggestions"];
      return value;
    },
  },
  methods: {
    clickAway() {
      this.showSuggestion = false;
    },
    search(e) {
      if (e.key === "Enter" || e.keyCode === 13) {
        this.hasValue = false;
        this.searchMedicine();
        this.showSuggestion = false;
        // @keydown.enter="$event.target.blur()"
      }
    },
    setSearchInputValue(value) {
      this.searchValue = value;
      this.hasValue = false;
      this.searchMedicine();
    },
    addToCart() {
      this.$store.dispatch("searchMedMod/addMedsToSelected", {
        brandName: this.searchValue,
        valid: false,
      });
    },
    searchMedicine() {
      // alert("searching ...");
      // this.showLoading=true;

      if (this.currentTab == "med") {
        this.$eventbus.$emit("loading-med", true);
        this.$eventbus.$emit("search-indicator", false);
        this.$store
          .dispatch("searchMedMod/getMedByName", this.searchValue)
          .finally(() => {
            this.$eventbus.$emit("loading-med", false);
          });
      } else if (this.currentTab == "phar") {
        this.$eventbus.$emit("loading-phar", true);
        this.showSearchIndicatorphar = false;
        this.$store
          .dispatch(
            "searchMedicalCenterMod/getPharmacyByName",
            this.searchValue
          )
          .finally(() => {
            this.$eventbus.$emit("loading-phar", false);
          });
      } else {
        this.$eventbus.$emit("loading-med-Centers", true);
        this.showSearchIndicatorMedCenter = false;
        this.$store
          .dispatch(
            "searchMedicalCenterMod/getMedCenterByName",
            this.searchValue
          )
          .finally(() => {
            this.$eventbus.$emit("loading-med-Centers", false);
          });
      }
    },
    mActivate(value) {
      this.showModal = value;
    },
    medData(value) {
      console.log(value);
      this.medInfo = value;
    },
    searchByCategory() {
      console.log("cat");
      console.log(this.$route.query.medname);
      var medName = this.$route.query.medname;
      if (medName) {
        this.$nextTick(() => {
          console.log("AAAAAAAAAAAAAA", medName);
          this.$eventbus.$emit("loading-med", true);
          this.$eventbus.$emit("search-indicator", false);
          this.$store
            .dispatch("searchMedMod/getMedByCategory", {
              subcategoryId: medName,
            })
            .then((result) => {
              console.log(result);
              this.$eventbus.$emit("loading-med", false);
            });
        });
      } else {
        this.$nextTick(() => {
          this.$eventbus.$emit("search-indicator", true);
        });
      }
    },
  },
  watch: {
    $route() {
      this.queries = this.$route.query;
      this.searchByCategory();
    },
    queries() {
      this.currentTab = this.queries.show;
    },
    searchValue() {
      this.hasValue = false;
      if (this.currentTab == "med") {
        this.$store
          .dispatch("suggestion/suggestion", this.searchValue)
          .finally(() => {
            this.$eventbus.$emit("loading-med", false);
          });
      }
    },
    suggestions() {
      if (this.suggestions.length > 0) {
        this.hasValue = true;
      } else {
        this.hasValue = false;
      }
    },
  },
};
</script>
