<template>
  <div class="conainer">
    <div class="flex justify-center">
      <loading-animation
        class="bg-white rounded-full p-3 bg-opacity-75"
        v-if="showLoading"
      ></loading-animation>
    </div>
    <div class="flex flex-wrap gap-4 justify-center">
      <div class="">
        <search-place-holder
          v-if="showSearchIndicatorMedCenter && phar.length == 0"
          placeholder="Search your M-center"
        ></search-place-holder>
      </div>

      <div v-for="(result, index) in phar" :key="index">
        <div class="max-w-lg h-40">
          <medical-center-card :data="result"></medical-center-card>
        </div>
      </div>
    </div>
    <div class="flex justify-center mb-9 mt-9">
      <button
        v-if="MedCentersData.next_page_url"
        class="px-3 py-2 hover:bg-gray-400 bg-gray-300 text-gray-700 rounded-md hover:text-white"
        @click="nextPage"
      >
        more Medical Center
      </button>
    </div>
  </div>
</template>
<script>
import MedicalCenterCard from "../../components/MedicalCenter/MedicalCenterCard.vue";
import SearchPlaceHolder from "../../components/SearchPlaceHolder";
import LoadingAnimation from "../../components/LoadingAnimation.vue";
import { mapGetters } from "vuex";
export default {
  components: { MedicalCenterCard, SearchPlaceHolder, LoadingAnimation },
  data() {
    return {
      showLoading: false,
    };
  },
  methods: {
    nextPage() {
      this.$store
        .dispatch("searchMedicalCenterMod/nextMedicalCenter")
        .then((res) => {
          console.log(res);
        });
    },
  },
  computed: {
    ...mapGetters("searchMedicalCenterMod", {
      phar: "getMedCenters",
      MedCentersData: "getMedCentersData",
    }),
  },
  props: {
    showSearchIndicatorMedCenter: {
      type: Boolean,
      default: true,
    },
  },
  mounted() {
    this.$eventbus.$on("loading-med-Centers", (val) => {
      this.showLoading = val;
    });
  },
};
</script>