<template>
  <layer>
    <div class="flex justify-center">
      <div class="max-w-6xl p-20">
        <div
          class="flex w-full h-96 justify-center items-center"
          v-if="loading"
        >
          <loading-animation></loading-animation>
        </div>
        <div v-else-if="medicalCenters.length == 0" class="max-w-2xl">
          <empty placeholder="sorry! no favorite pharmacy"></empty>
        </div>
        <div v-else>
          <div
            class="flex w-full py-4 text-medehanite-primary font-medium text-4xl justify-center"
          >
            Favorite pharmacy
          </div>
          <div
            v-for="value in medicalCenters"
            :key="value.id"
            class="flex flex-col gap-9"
          >
            <div class="h-40">
              <medical-center-card :data="value"> </medical-center-card>
            </div>
            <div class="h-1 w-full"></div>
          </div>
          <div class="flex justify-center mb-9">
            <button
              v-if="medicalCenterData.next_page_url"
              class="px-3 py-2 hover:bg-gray-400 bg-gray-300 text-gray-700 rounded-md hover:text-white"
              @click="nextPage"
            >
              more pharmacies
            </button>
          </div>
      </div>
      </div>
    </div>
  </layer>
</template>
<script>
import { mapGetters } from "vuex";
import LoadingAnimation from "../components/LoadingAnimation.vue";
import Layer from "../layouts/Main.vue";

import MedicalCenterCard from "../components/MedicalCenter/MedicalCenterCard.vue";
import Empty from "../components/Empty.vue";
export default {
  components: {
    Layer,
    LoadingAnimation,
    MedicalCenterCard,
    Empty,
  },
  data() {
    return {
      loading: true,
    };
  },
  methods:{
    nextPage(){
      this.$store.dispatch("favMod/nextFavorite").then((res)=>{
        console.log(res)
      });
    }
  },
  computed: {
    ...mapGetters("favMod", { medicalCenters: "medicalCenters" , medicalCenterData:"medicalCenterData"}),
  },
  created() {
    this.$store.dispatch("favMod/getFavoriteMedicalCenters").then(() => {
      this.loading = false;
    });
  },
};
</script>