<template>
  <label for="inputer">
    <span class="mb-2 text-gray-500">{{label | capitalizer}}</span>
    <div class="relative text-gray-600 ">
      <span class="absolute inset-y-4 left-0 flex items-center pl-1 py-2">
        <div class="pl-4 float-left">
          <slot></slot>
        </div>
      </span>
      <input
        id="inputer"
        type="text"
        name=""
        v-bind="$attrs"
        :placeholder="placeholder | capitalizer"
        class="rounded-md h-14 w-full pl-12 focus:pl-6 focus:outline-none border-medehanite-inputFieldBorder border-2 bg-medehanite-inputFieldBackgound focus:bg-white focus:border-medehanite-primary"
      />
    </div>
  </label>
</template>
<script>
export default {
  name: "InputFieldWithIcon",
  inheritAttrs: false,
  props: {
    value:Object,
    placeholder:{
      type:String,
      default: 'Type here'
    },
    type:String ,
    label:String
  },
  computed: {
    inputListeners: function() {
      // `Object.assign` merges objects together to form a new object
      return Object.assign(
        {},
        // We add all the listeners from the parent
        this.$listeners,
        // Then we can add custom listeners or override the
        // behavior of some listeners.
        {
          // This ensures that the component works with v-model
          input: (event) => {
            this.$emit("input", event.target.value);
          },
        }
      );
    },
  },
  filters:{
    capitalizer(value){
      if(!value) return ''
      value = value.toString();
     var text = value.toLowerCase()
    .split(' ')
    .map((s) => s.charAt(0).toUpperCase() + s.substring(1))
    .join(' ');
      return text;

    }
  }
};
</script>
