                      d="M13,26A13,13,0,0,1,3.808,3.808,13,13,0,1,1,22.192,22.192,12.915,12.915,0,0,1,13,26ZM12.523,9.915a.324.324,0,0,1,.328.207,1.316,1.316,0,0,1,0,.689c-.046.186-.425,1.231-1.127,3.1l-1.057,2.83a2.347,2.347,0,0,0,0,1.035,1.677,1.677,0,0,0,.367.735,1.928,1.928,0,0,0,1.1.668,2.759,2.759,0,0,0,.6.046,3.341,3.341,0,0,0,.621-.046,3.483,3.483,0,0,0,1.31-.806,5.61,5.61,0,0,0,1.381-2.461c.046-.2.03-.331-.047-.392a1.218,1.218,0,0,0-.482-.046,2,2,0,0,0-.414.023c-.077.015-.154.139-.23.368a.831.831,0,0,1-.068.207,2.856,2.856,0,0,1-1.518,2.047,1.332,1.332,0,0,1-.322.023.483.483,0,0,1-.253-.047c-.168-.168-.16-.555.023-1.149.015-.078.371-1.03,1.057-2.83l1.035-2.805a2.272,2.272,0,0,0,0-1.012A1.984,1.984,0,0,0,13.15,8.857c-.133-.012-.259-.018-.373-.018a2.2,2.2,0,0,0-.455.042,1.823,1.823,0,0,0-.714.253c-.093.047-.178.093-.253.138a4.842,4.842,0,0,0-1.863,2.783c-.093.264-.1.422-.023.483l.068.069h.437a1.206,1.206,0,0,0,.483-.046.9.9,0,0,0,.092-.161A5.175,5.175,0,0,1,11.1,11a2.01,2.01,0,0,1,1.242-1.058A.79.79,0,0,1,12.523,9.915ZM14.716,3.8a1.44,1.44,0,0,0-.347.044,1.534,1.534,0,0,0-1.1.92,1.067,1.067,0,0,0,.878,1.662,1.369,1.369,0,0,0,.364-.052A.2.2,0,0,1,14.6,6.35a1.679,1.679,0,0,0,1.149-1.174c.015-.06.03-.129.046-.207a.967.967,0,0,0-.552-1.057A1.285,1.285,0,0,0,14.716,3.8Z"
