                  >
                    <path
                      id="Union_1"
                      data-name="Union 1"
                      d="M-4160-4498v-18h-18.5a8.5,8.5,0,0,1-8.5-8.5,8.5,8.5,0,0,1,8.5-8.5h18.5v-19a8,8,0,0,1,8-8,8,8,0,0,1,8,8v19h18.5a8.5,8.5,0,0,1,8.5,8.5,8.5,8.5,0,0,1-8.5,8.5H-4144v18a8,8,0,0,1-8,8A8,8,0,0,1-4160-4498Z"
