                    />
                  </svg>
                </button>
              </div>

              <img
                :src="storageURL + result.image"
                class="h-48 w-full bg-medehanite-light rounded-t-3xl pt-4 m-auto object-scale-down hover:object-contain card-header"
              />

              <div class="w-full bg-white">
                <div class="px-3 mx-auto">
                  <div
                    class="w-full text-2xl font-semibold pt-5 pb-2 text-center truncate"
                  >
                    {{ result.brandName }}
                    <hr class="w-1/2 mx-auto bg-medehanite-primary h-1 mt-2" />
                  </div>
                </div>
              </div>

              <div
                class="bg-white text-base px-3 py-3 rounded-b-3xl text-gray-600"
              >
                <div class="text-center">
                  <span class="font-bold pr-1 text-black">GenericName:</span>

                  <span class="text-left"> {{ result.genericName }}</span>
                </div>
                <div class="text-center">
                  <span class="font-bold pr-1 text-black">Manufacturer:</span>
                  {{ result.manufacturer }}
                </div>
              </div>
            </div>
          </div>
        </div>
        <search-place-holder
          v-if="showSearchIndicator && !indiator"
          placeholder="Search your Med"
        ></search-place-holder>
        <div class="h-44 w-52 mb-32" v-else>
          <empty v-if="!showLoading && medData.length==0"></empty>
        </div>
      </div>
      <!-- <div class="fixed inset-x-0 h-screen top-0 left-0"> -->
      <div
        class="fixed bottom-10 right-1 z-50 rounded-lg transition-all duration-1000"
        v-if="selectedHasValue"
      >
        <div
          class="border-medehanite-primary rounded-2xl bg-white border-4 p-1 overflow-y-scroll"
          style="max-height: 50vh"
          :class="{ compress: !shapeChange }"
        >
          <div
            v-for="(value, index) in selectedMeds"
            :key="index"
            class="relative"
          >
            <div v-if="value.valid == true">
              <add-card
                :valid="true"
                :name="value.brandName"
                :image="storageURL + value.image"
                class="my-1 -left-0"
              ></add-card>
            </div>
            <div v-if="value.valid == false">
              <add-card
                :valid="false"
                :name="value.brandName"
                class="my-1 -left-0"
              ></add-card>
            </div>
          </div>
        </div>
        <button
          @click="shapeChange = !shapeChange"
          class="bg-medehanite-primary -rotate-90 my-2 w-full focus:bg-medehanite-focused h-14 px-2 text-white rounded-lg justify-center flex"
          :class="{ 'rotate-180': shapeChange }"
        >
          <svg
            class="my-auto"
            xmlns="http://www.w3.org/2000/svg"
            width="22.508"
            height="14.533"
            viewBox="0 0 22.508 14.533"
          >
            <path
              id="Icon_awesome-angle-up"
              data-name="Icon awesome-angle-up"
              d="M12.445,11.229l9.563,9.563a1.681,1.681,0,0,1,0,2.384l-1.589,1.589a1.681,1.681,0,0,1-2.384,0L11.25,17.993,4.472,24.771a1.681,1.681,0,0,1-2.384,0l-1.6-1.589a1.681,1.681,0,0,1,0-2.384l9.563-9.562a1.683,1.683,0,0,1,2.391-.007Z"
              transform="translate(0.004 -10.734)"
            />
          </svg>
        </button>
        <button
          class="bg-medehanite-primary my-2 w-full focus:bg-medehanite-focused h-14 px-2 text-white rounded-lg"
          @click="findInPhar"
        >
          Find In Pharmacy
        </button>
      </div>
      <!-- </div> -->
      <div class="flex justify-center mb-9">
        <button
          v-if="meds.next_page_url != null"
          class="px-3 py-2 hover:bg-gray-400 bg-gray-300 text-gray-700 rounded-md hover:text-white"
          @click="nextPage"
        >
          more medicine
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import AddCard from "../../components/Search/AddCard.vue";
import SearchPlaceHolder from "../../components/SearchPlaceHolder";
import { mapGetters } from "vuex";
import LoadingAnimation from "../../components/LoadingAnimation.vue";
import Empty from "../../components/Empty.vue";
// import Categories from "../../components/Categories.vue";

export default {
  components: {
    AddCard,
    SearchPlaceHolder,
    LoadingAnimation,
    Empty,
    // Categories
  },
  data() {
    return {
      shapeChange: true,
      showLoading: false,
      selectedHasValue: false,
      showSearchIndicator: true,
    };
  },
  props: {
    // showSearchIndicator: {
    //   type: Boolean,
    //   default: true,
    // },
  },
  methods: {
    addToCart(id) {
      this.$store.dispatch("searchMedMod/addMedsToSelected", {
        ...this.medData.filter((x) => x.id == id)[0],
        valid: true,
      });
    },
    addToCartWithName(value) {
      this.$store.dispatch("searchMedMod/addMedsToSelected", {
        brandName: value,
        valid: false,
      });
    },
    nextPage() {
      this.$store.dispatch("searchMedMod/nextPage");
    },
    findInPhar() {
      var foundVar = this.selectedMeds
        .map((val) => {
          if (val.valid) {
            return val.id;
          }
        })
        .filter((x) => x)
        .toString();
      var notFoundVar = this.selectedMeds
        .map((val) => {
          if (!val.valid) {
            return val.brandName;
          }
        })
        .filter((x) => x)
        .toString();
      console.log(foundVar);
      this.$router.push({
        name: "fi-pharmacy",
        query: { found: foundVar, notFound: notFoundVar },
      });
    },
    sendMedData(value) {
      this.$emit("medDetail", value);
      this.$emit("modalActivate", true);
    },
  },
  computed: {
    ...mapGetters("searchMedMod", {
      medData: "medData",
    }),
    ...mapGetters("searchMedMod", {
      meds: "meds",
      indiator: "indicator",
    }),
    ...mapGetters("searchMedMod", {
      selectedMeds: "selectedMeds",
    }),
    ...mapGetters("searchMedMod", {
      selectedMeds: "selectedMeds",
    }),

    storageURL() {
      return process.env.VUE_APP_STORAGE_BASE_URL;
    },
  },

  watch: {
    selectedMeds() {
      if (this.selectedMeds.length > 0) {
        this.selectedHasValue = true;
      } else {
        this.selectedHasValue = false;
      }
    },
  },
  mounted() {
    this.$eventbus.$on("loading-med", (val) => {
      this.showLoading = val;
    });
    this.$eventbus.$on("search-indicator", (val) => {
      this.showSearchIndicator = val;
    });
  },
  created() {
    if (this.selectedMeds.length > 0) {
      this.selectedHasValue = true;
    } else {
      this.selectedHasValue = false;
    }
  },
};
</script>

<style scoped>
.result-card:hover > .result-card-cover {
  @apply visible;
}
.compress {
  height: 10vh;
}
