                            d="M5.455,16.318.235,11.1a.8.8,0,0,1,0-1.136L1.371,8.827a.8.8,0,0,1,1.136,0l3.516,3.516,7.531-7.531a.8.8,0,0,1,1.136,0l1.136,1.136a.8.8,0,0,1,0,1.136L6.59,16.318A.8.8,0,0,1,5.455,16.318Z"
