                </div>
              </div>
            </div>
  
            <div class="mt-4  items-center justify-center space-x-2 gap-12 flex">
              <button
                class="h-12 w-32 text-medehanite-primary font-medium text-base text-opacity-80 hover:text-opacity-100"
                @click="close()"
              >
                Go Back
              </button>
              <button
                @click="addToCart"
                class="bg-medehanite-light h-12 w-32 text-medehanite-primary font-medium text-base rounded-lg bg-opacity-80 hover:bg-opacity-100"
              >
                Add
              </button>
            </div>
          </div>
        </transition>
      </div>
    </div>
  </transition>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  props: {
    show: {
      type: Boolean,
      default: true,
    },
    detail: {
      type: Object,
      default: () => {
        return {
          brandName: "",
        };
      },
    },
  },
  components: {},

  data: () => ({
    storageURL: "",
    working: false,
    medicineData: {
      description: `Ibuprofen is a nonsteroidal anti-inflammatory drug (NSAID). It works by reducing hormones that cause inflammation and pain in the body.
                    Ibuprofen is used to reduce fever and treat pain or inflammation caused by many conditions such as headache, toothache, back pain, arthritis, menstrual cramps, or minor injury.
                    Ibuprofen is used in adults and children who are at least 6 months old.`,
      warnings: [
        "changes in your vision;",

        "shortness of breath (even with mild exertion);",

        "swelling or rapid weight gain;",

        "the first sign of any skin rash, no matter how mild;",

        "signs of stomach bleeding - bloody or tarry stools, coughing up blood or vomit that looks like coffee grounds;",

        "liver problems - nausea, upper stomach pain, itching, tired feeling, flu-like symptoms, loss of appetite, dark urine, clay-colored stools, jaundice (yellowing of the skin or eyes);",

        "kidney problems - little or no urinating, painful or difficult urination, swelling in your feet or ankles, feeling tired or short of breath;",

        "low red blood cells (anemia) - pale skin, feeling light-headed or short of breath, rapid heart rate, trouble concentrating; or",

        "severe skin reaction - fever, sore throat, swelling in your face or tongue, burning in your eyes, skin pain followed by a red or purple skin rash that spreads (especially in the face or upper body) and causes blistering and peeling.",
      ],
    },
  }),
  created() {},
  mounted() {
    this.storageURL = process.env.VUE_APP_STORAGE_BASE_URL;
  },
  computed: {
    ...mapGetters("searchMedMod", {
      medData: "medData",
    }),
    descriptionFormatted() {
      return this.medicineData.description
        .split("\n")
        .map((x) => `<div>${x}</div>`)
        .join("<br/>");
    },
  },

  methods: {

    open() {
      this.show = true;
    },
    close() {
      if (this.working) return;

      this.show = false;
    },
    addToCart() {
      this.$store.dispatch("searchMedMod/addMedsToSelected", {
        ...this.medData.filter((x) => x.id == this.detail.id)[0],
        valid: true,
      });
      this.close();
    },

  },
  watch: {},
};
</script>
<style  scoped>
@media only screen and (max-width: 640px) {
.heighter{
  height: 83vh !important;

}
}
